button {
  background: none;
  border: none;
  cursor: pointer;
}

.audio-player {
  position: relative;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.title {
  font-family: "ComicR";
  color: #fff;
  font-size: 1.5vw;
  letter-spacing: 0;
  line-height: 0;
  margin: 0;
  padding-bottom: 1vw;
}

.controls-container {
  height: 100%;
  width: 15vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.audio-controls {
  display: flex;
  justify-content: space-between;
  width: 10vw;
  margin: 0;
  padding: 0;
}

.audio-controls .prev svg,
.audio-controls .next svg {
  width: 2vw;
  height: 2vw;
}

.audio-controls .play svg,
.audio-controls .pause svg {
  height: 1.5vw;
  width: 1.5vw;
}

.audio-controls path {
  fill: #FFF;
}