.termspopup-app {
  position: fixed;
  bottom: 0;
  width: 100vw;
  background-color: #222;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  opacity: 0.95;
}

.termspopup-container {
  position: relative;
  width: 100%;
  padding: 2% 5%;
}

.termspopup-container-col {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 1;
}


/* TEXT */

.termspopup-text-title {
  font-family: "ComicR";
  font-size: 4vw;
  font-weight: bold;
  color: #DDD;
  margin: 0;
  padding: 0;
  padding-bottom: 1%;
}

.termspopup-text-description {
  font-size: 1.6vw;
  color: #DDD;
  margin: 0;
  padding: 0;
}

.termspopup-button {
  height: 3.5vw;
  width: auto;
  cursor: pointer;
  color: #DA0505;
  padding: 1vw;
  border: 0;
  font-family: "ComicR";
  font-size: 3vw;
  font-weight: bold;
  letter-spacing: 2px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}



/* SCREEN SIZE */

@media (max-width:768px){
  
  .termspopup-text-title {
    font-size: 6vw;
    padding: 2%;
  }
  
  .termspopup-text-description {
    font-size: 3vw;
    padding: 2%;
    text-align: center;
  }
  
  .termspopup-button {
    height: 6vw;
    padding: 0 6vw;
    font-size: 4vw;
    margin: 2%;
  }
}

@media (max-width:576px){

  .termspopup-text-title {
    font-size: 8vw;
    padding: 2%;
  }
  
  .termspopup-text-description {
    font-size: 4vw;
    padding: 2%;
    text-align: center;
  }
  
  .termspopup-button {
    height: 8vw;
    padding: 0 8vw;
    font-size: 5vw;
    margin: 2%;
  }
}