.footer-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #000;
  padding: 0;
  margin: 0;
}

.footer-container {
  width: 100%;
  height: 100%;
  padding: 3% 10%;
}

.footer-left {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.footer-left-project {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.footer-left-copyright {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-top: 30%;
  margin-left: 3%;
}

.footer-right {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-right: 20%;
}


/* LEFT */

.footer-left-img {
  position: relative;
  width: 18%;
  height: 100%;
  padding-right: 5%;
}

.footer-left-title {
  font-family: "Tokyo";
  font-size: 1.5vw;
  color: #fff;
  margin: 0;
  padding: 0;
}

.footer-left-text {
  font-family: "ComicR";
  font-size: 1.2vw;
  color: #ddd;
  margin: 0;
  padding: 0;
}

.footer-copyright {
  font-family: "ComicR";
  font-size: 1.2vw;
  color: #fff;
  margin-top: auto;
}


/* RIGHT */

.footer-right-text {
  font-family: "ComicR";
  font-size: 1.5vw;
  color: #fff;
  margin: 5% 0;
}

.footer-svg {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2vw;
}

.footer-svg-container {
  position: relative;
  height: 100%;
  margin: 0 10%;
}

.footer-svg-img {
  width: 2.5vw;
  height: 100%;
}


/* CHN */

.footer-right-chn {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.footer-right-img {
  position: relative;
  width: 50%;
}








/* SCREEN SIZE */

@media (max-width:768px){

  .footer-app {
    position: relative;
    width: 100vw;
    height: 100%;
    background-color: #000;
    padding: 0;
    margin: 0;
  }
  
  .footer-container {
    width: 100%;
    height: 100%;
    padding: 3% 10%;
  }

  .footer-left {
    padding: 10% 0;
  }
  
  .footer-left-project {
    justify-content: center;
    align-items: center;
  }
  
  .footer-left-copyright {
    justify-content: center;
    margin-top: 5%;
    margin-left: 0%;
  }
  
  .footer-right {
    justify-content: center;
    align-items: center;
    padding-right: 0%;
  }

  .footer-left-img {
    width: 15%;
    padding-right: 3%;
  }
  
  .footer-left-title {
    font-size: 5.5vw;
  }
  
  .footer-left-text {
    display: none;
  }
  
  .footer-copyright {
    font-size: 3.8vw;
    text-align: center;
  }
  
  .footer-right-text {
    font-size: 6vw;
    margin: 5% 0;
    text-align: center;
  }
  
  .footer-svg {
    width: 100%;
    justify-content: space-between;
  }
  
  .footer-svg-container {
    height: 100%;
    margin: 10% 10%;
  }
  
  .footer-svg-img {
    width: 8vw;
    height: 100%;
  }
  
  .footer-right-chn {
    display: none;
  }
}