.home-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #1E1C1C;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-container {
  padding-top: 10%;
  overflow: hidden;
}

.home-container-left {
  position: relative;
  width: 55vw;
  height: 100%;
  padding-left: 8vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.home-container-right {
  position: relative;
  width: 50vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}


/* CHINESE SIGN */

.home-container-chn {
  position: absolute;
  height: 75%;
  left: 0.25vw;
  z-index: 1;
}


/* CHARACTERS */

.home-character-imgCenter {
  width: 100%;
}

.home-character-Center-img {
  width: 65%;
  z-index: 3;
  margin-top: auto;
  margin-right: 5vw;
}

.home-character-imgLeft {
  width: 100%;
}

.home-character-Left-img {
  position: absolute;
  bottom: 0;
  left: -17.5%;
  z-index: 2;
  width: 58%;
}

.home-character-imgRight {
  width: 100%;
}

.home-character-Right-img {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
  width: 58%;
}

.home-character-imgLeft-task {
  width: 100%;
}

.home-character-Left-task {
  position: absolute;
  bottom: 20%;
  z-index: 0;
  width: 100%;
  left: -20%;
}

.home-character-imgRight-task {
  width: 100%;
}

.home-character-Right-task {
  position: absolute;
  bottom: 20%;
  z-index: 0;
  width: 100%;
  right: -10%;
}




/* TITLES  & BUTTON */

.home-container-titles-bottom {
  width: 100%;
  margin-top: auto;
  margin-bottom: 20vw;
}

.home-text-title-chn {
  font-size: 4.5vw;
  line-height: 5vw;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: "Tokyo";
}

.home-text-title {
  font-size: 3.4vw;
  line-height: 5vw;
  color: #fff;
  margin: 0;
  padding-left: 0.3vw;
  font-family: "Tokyo";
}

.home-text-description {
  font-size: 1.7vw;
  padding-left: 0.5vw;
  padding-top: 2vw;
  color: #fff;
  font-family: "ComicR";
}

.home-connect-wallet-button {
  height: 3vw;
  width: auto;
  cursor: pointer;
  background: #fff;
  color: #111;
  padding: 0 2.5vw;
  margin-top: 8vw;
  border: 0;
  border-radius: 4px;
  font-family: "ComicR";
  font-size: 1.5vw;
  font-weight: bold;
  letter-spacing: 2px;
  margin-left: 0.5vw;
}


/* RED LINE */

.home-red-line {
  position: absolute;
  width: 100%;
  height: 12vw;
  background-color: #DA0505;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.home-red-line-text {
  max-width: 45vw;
  font-size: 2.5vw;
  line-height: 3vw;
  color: #fff;
  margin: 0;
  padding-left: 8vw;
  font-family: "ComicR";
}




/* SCREEN SIZE CHANGER */

@media (max-width:767px){

  .home-container-left {
    width: 100vw;
    padding-left: 0;
  }
  
  .home-container-right {
    width: 100vw;
  }
  
  .home-container-chn {
    display: none;
  }
  
  /* CHARACTERS */

  .home-character-Center-img {
    margin-right: 0;
  }
  
  .home-character-Left-img {
    left: -13%;
  }

  .home-character-Right-img {
    right: -5%;
  }
  
  .home-character-Left-task {
    left: -15%;
  }
  
  .home-character-Right-task {
    right: -15%;
  }


  .home-container-titles-bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 15vw;
  }

  .home-text-title-chn {
    font-size: 13vw;
    line-height: 15vw;
    color: #fff;
    margin: 0;
    padding: 0;
    font-family: "Tokyo";
  }
  
  .home-text-title {
    font-size: 10vw;
    line-height: 16vw;
    text-align: center;
  }
  
  .home-text-description {
    font-size: 4.5vw;
    padding-left: 0;
    padding-top: 3vw;
    text-align: center;
  }
  
  .home-connect-wallet-button {
    height: 10vw;
    padding: 0 10vw;
    margin-top: 10vw;
    font-size: 5vw;
    margin-left: 0;
  }
  
  
  /* RED LINE */
  
  .home-red-line {
    position: relative;
    width: 100%;
    height: 25vw;
    justify-content: center;
  }
  
  .home-red-line-text {
    max-width: 100%;
    font-size: 6vw;
    line-height: 6vw;
    padding-left: 0;
    text-align: center;
  }
}