.about-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #000;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 2% 3%;
}

.about-container {
  width: 100%;
  height: 100%;
}

.about-container-right {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.about-container-left {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 5% 10%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}


/* CHINESE SIGN */

.about-container-chn {
  position: absolute;
  height: 25vw;
  right: 2vw;
}


/* IMAGES */

.about-container-right-img {
  position: relative;
  width: 40vw;
  padding: 1% 0;
}


/* TEXT */

.about-text-title {
  font-family: "Tokyo";
  font-size: 3.5vw;
  font-weight: bold;
  color: #fff;
  margin: 0;
  padding: 0;
  margin-bottom: 2vw;
}

.about-text-description {
  font-family: "ComicR";
  font-size: 2vw;
  color: #fff;
  padding-bottom: 1.5vw;
}



/* SCREEN SIZE */

@media (max-width:768px){
  
  .about-container {
    padding: 1%;
  }

  .about-container-left {
    padding: 5% 1%;
  }

  .about-container-chn {
    display: none;
  }

  .about-container-right-img {
    width: 80vw;
    padding: 5% 0;
  }

  .about-text-title {
    font-size: 10vw;
    margin: 10vw 0;
    text-align: center;
  }
  
  .about-text-description {
    font-size: 5.5vw;
    padding-bottom: 1.5vw;
    text-align: center;
  }
}