.terms-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #000;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.terms-container {
  position: relative;
  width: 80%;
  height: 100%;
  padding: 5% 0%;
}


/* TEXT */

.terms-text-title {
  font-family: "ComicR";
  font-size: 3.5vw;
  font-weight: bold;
  color: #DDD;
  padding: 0;
  margin-bottom: 1vw;
  margin-top: 2vw;
}

.terms-text-description {
  font-size: 1.2vw;
  color: #858383;
  padding-bottom: 1.5vw;
}




/* SCREEN SIZE */

@media (max-width:768px){
  
  .terms-container {
    width: 95%;
    padding: 10% 0%;
  }
  
  .terms-text-title {
    font-size: 8vw;
    margin-bottom: 1vw;
    margin-top: 5vw;
  }
  
  .terms-text-description {
    font-size: 5vw;
    padding-bottom: 1.5vw;
  }
}