.history-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #000;
  margin: 0;
  padding: 0;
}

.history-text-container {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.history-container-left {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 5vw;
}

.history-container-right {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-right: 5vw;
}

.history-img {
  margin-top: auto;
  width: 50%;
  box-shadow: 10px 10px 20px #111;
}

.history-text-title {
  position: absolute;
  width: 50%;
  z-index: 2;
  font-size: 3.4vw;
  line-height: 5vw;
  color: #fff;
  margin: 0;
  padding-left: 0.3vw;
  font-family: "ComicR";
}