.navbar-desktop {
  display: block;
}

.navbar-app {
  position: relative;
  width: 100vw;
  height: 4vw;
  z-index: 100;
}

.navbar-container {
  width: 100%;
  height: 100%;
  background-color: #252323;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  padding: 0 5%;
}

.navbar-container-menu-text {
  text-align: center;
  font-family: "ComicR";
  font-size: 2vw;
  color: #fff;
  margin: 0;
  padding: 0;
  line-height: 0;
}

.navbar-container-menu-text p:hover {
  background-color: gold;
}

.navbar-container-menu-wallet {
  text-align: center;
  font-family: "ComicR";
  font-size: 2vw;
  color: #DA0505;
  margin: 0;
  padding: 0;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  line-height: 0;
  margin-left: auto;
}

.navbar-container-logo-img {
  width: 5vw;
  margin-top: 2vw;
  background-color: #252323;
  border-radius: 50%;
}

.navbar-container-audioplayer {
  width:0;
	transition: 0.5s;
  transform: scaleX(0);
}

.navbar-container-audioplayer-scale {
  position: relative;
  left:0;
	transition: 0.5s;
  transform: scaleX(1);
}


/* MOBILE */

.navbar-mobile {
  display: none;
}

.navbar-mobile-app {
  position: relative;
  width: 100vw;
  height: 100%;
  z-index: 100;
}

.navbar-mobile-container {
  width: 100%;
  height: 100%;
  background-color: #252323;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 1% 5%;
}

.navbar-mobile-logo-img {
  width: 10vw;
  margin-right: 5%;
  margin-left: 0.25vw;
}

.navbar-mobile-menu-text {
  text-align: center;
  font-family: "ComicR";
  font-size: 8vw;
  color: #fff;
  margin: 0;
  padding: 0;
  line-height: 15vw;
}

.navbar-mobile-menu-next {
  position: relative;
  margin: 5%;
  padding: 2% 0;
  border: 2px solid #ddd;
}

.navbar-mobile-menu-next-text {
  text-align: center;
  font-family: "ComicR";
  font-size: 4vw;
  color: #fff;
  margin: 0;
  padding: 0;
  line-height: 8vw;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: relative;
  width: 8vw;
  height: 7vw;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #DA0505;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 3vw;
  width: 3vw;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #000;
  height: 100%;
  padding: 10vw 2vw;
}

.bm-menu-wrap {
  position: absolute;
  top: 0;
  height: 100%;
}



