/* SCREEN SIZE */

@media (max-width:768px){

  .navbar-desktop {
    display: none;
  }
  
  .navbar-mobile {
    display: block;
  }

  .navbar-container-menu-wallet {
    text-align: center;
    font-family: "ComicR";
    font-size: 8vw;
    color: #DA0505;
    margin: 0;
    padding: 10vw 0;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    line-height: 8vw;
  }
}

