.reader-app {
  position: relative;
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  background-image: url("../../assets/mountains.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.reader-container {
  width: 100%;
  height: 100%;
  padding: 1%;
}

.reader-container-right {
  position: relative;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.reader-container-left {
  position: relative;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}



/* TASK LEFT */

.reader-container-task-black {
  width: 16vw;
  z-index: 1;
}

.reader-container-task-chn {
  position: absolute;
  height: 22vw;
  padding-right: 2vw;
  z-index: 2;
}


/* TASK RIGHT */

.reader-container-task-text {
  position: relative;
  width: 60vw;
  z-index: 1;
}

.reader-container-task-text-logo {
  position: absolute;
  height: 5vw;
  top: 15vw;
  margin-left: 5vw;
  z-index: 2;
}

.reader-container-task-text-description {
  position: absolute;
  text-align: center;
  width: 25vw;
  bottom: 12vw;
  margin-left: 5vw;
  z-index: 2;
  font-size: 1.5vw;
  color: #ffffff;
  font-family: "ComicR";
}


/* RED LINE */

.reader-red-line {
  position: absolute;
  width: 10vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  right: 0;
  top: 0;
  background-color: #DA0505;
  opacity: 0.7;
  z-index: 3;
}

.reader-red-line-text {
  font-size: 1vw;
  line-height: 2vw;
  color: #ffffff;
  margin: 0;
  font-family: "Tokyo";
}






@media (max-width:768px){
  
  .reader-app {
    padding: 20% 0;
  }

  .reader-container-right {
    padding: 0;
    overflow-x: hidden;
  }
  
  .reader-container-left {
    padding: 0;
  }

  .reader-container-task-black {
    width: 30vw;
  }
  
  .reader-container-task-chn {
    height: 40vw;
    padding-right: 2vw;
  }

  .reader-container-task-text {
    width: 140vw;
    margin-right: 20vw;
  }
  
  .reader-container-task-text-logo {
    height: 15vw;
    top: 30vw;
    margin-left: 0;
  }
  
  .reader-container-task-text-description {
    width: 80vw;
    bottom: 25vw;
    margin-left: 0;
    font-size: 4.5vw;
  }

  .reader-red-line {
    position: absolute;
    width: 100%;
    height: 20vw;
    left: 0;
    bottom: 0;
    right: auto;
    top: auto;
  }
  
  .reader-red-line-text {
    font-size: 4.5vw;
    line-height: 5vw;
  }
}